import { createSelector } from "reselect";
import { fromJS } from "immutable";
import { initialState } from "./reducer";

/**
 * Direct selector to the languageToggle state domain
 */
export const selectNavigationConfig = (state = fromJS({})) =>
  state.get("navigationConfig", initialState);

/**
 * Select the language locale
 */

export const makeSelectNavigationConfig = () =>
  createSelector(selectNavigationConfig, (substate = fromJS({})) => substate.toJS());