import { ASK_AIRA_ENDPOINT } from './constants';

export const getRuleApi = (request, getAPICallObject) => ({
  activity,
  query,
}) => {
  const url = `${ASK_AIRA_ENDPOINT}/rule_expr/write_rule`;
  return request(
    url,
    getAPICallObject('POST', { query, save: true, activity }),
  );
};

export const getRuleSummarizeApi = (request, getAPICallObject) => ({
  activity,
  rule,
}) => {
  const url = `${ASK_AIRA_ENDPOINT}/rule_expr/summarize_rule`;
  return request(url, getAPICallObject('POST', { rule, activity }));
};

export const updateLikeResultApiRule = (
  request,
  getAPICallObject,
) => requestId => {
  const url = `${ASK_AIRA_ENDPOINT}/rule_expr/like`;
  return request(url, getAPICallObject('POST', { requestId }));
};

