import React from 'react';
import CapRow from '../CapRow';
import Divider from './Divider';
import L1ModuleWrapper from './L1ModuleWrapper';
import './_index.scss';
import { L1IconWrapperSize } from './constants';

const BottomSectionProductModules = ({
  currentState,
  dispatchAction,
  handleRedirectionAndModuleSelection,
  redirectToLink,
  pushDataToGTM,
  leftNavbarExpanded
}) => {
  const filteredConfig = currentState.sortedConfig?.filter(
    x => x.placement === 'bottom',
  );
  const l1Height = filteredConfig.length * L1IconWrapperSize;
  return (
    <>
      <Divider />
      <CapRow
        className="l2-product-row"
        style={{
          height: `calc(100vh - ${l1Height}px`,
        }}
      >
        {filteredConfig?.map(
          (module, index) =>
            module?.isVisible &&
            module?.isActive && (
              <L1ModuleWrapper
                moduleObj={module}
                index={index}
                key={`BottomProductMode_${module?.name}`}
                currentState={currentState}
                dispatchAction={dispatchAction}
                handleRedirectionAndModuleSelection={
                  handleRedirectionAndModuleSelection
                }
                redirectToLink={redirectToLink}
                pushDataToGTM={pushDataToGTM}
                leftNavbarExpanded={leftNavbarExpanded}
              />
            ),
        )}
      </CapRow>
    </>
  );
};

BottomSectionProductModules.defaultProps = {
  currentState: {},
  dispatchAction: () => {},
  handleRedirectionAndModuleSelection: () => {},
  redirectToLink: () => {},
  pushDataToGTM: () => {},
};

export default BottomSectionProductModules;
