import { createSelector } from 'reselect';
import { fromJS } from 'immutable';

import { selectAskAiraDomain } from '../selector';
import { DOC_BOT_REDUCER_DOMAIN } from './constants';

const selectMessagesDomain = (state = fromJS({})) =>
    selectAskAiraDomain(state)[DOC_BOT_REDUCER_DOMAIN];

const makeSelectMessageDetail = () =>
    createSelector(selectMessagesDomain, (substate = fromJS({})) =>
        substate.toJS(),
    );
export { selectMessagesDomain, makeSelectMessageDetail };
