export const sortModulesByDisplayOrder = (config = []) =>
  config.sort((a, b) => a?.displayOrder - b?.displayOrder);

export const removeCommonPath = (publicPath, url) => {
  const publicPathArray = publicPath
    .split('/')
    .filter(element => element !== '');
  const urlArray = url.split('/').filter(element => element !== '');
  const uniquePart = remove(urlArray, path => !publicPathArray.includes(path));
  return `/${uniquePart.join('/')}`;
};

export const validateDisabledConfig = (disabledConfig = {}) => {
  const {
    hoverHeadingLabel,
    hoverHeadingText,
    hoverDescriptionLabel,
    hoverDescriptionText,
    hoverCTADisplayLabel,
    hoverCTADisplayName,
    hoverCTAAction,
  } = disabledConfig;
  if (
    (hoverHeadingLabel || hoverHeadingText) ||
    (hoverDescriptionLabel || hoverDescriptionText) ||
    ((hoverCTADisplayLabel || hoverCTADisplayName) && hoverCTAAction)
  ) {
    return true;
  }
  return false;
};

