const extractDynamicValues = (inputString) => {
  const pattern = /{{(.*?)}}/g;
  return (inputString.match(pattern) || []).map(match => match.slice(2, -2));
};

const matchAndExtractMappedValue = ({ currentDomainBlockNo = -1, mappings = {} }) => {
  const replacerValue = window.location.host.split('.')?.[currentDomainBlockNo - 1];
  if (!isEmpty(replacerValue) && mappings.hasOwnProperty(replacerValue)) {
    return mappings[replacerValue];
  }
  return '';
}
export const getDifferentDomainUrl = ({ domainPattern, patternMapper }) => {
  const dynamicValues = extractDynamicValues(domainPattern);
  let domainPatternClone = domainPattern;
  dynamicValues?.map(dVal => {
    if (!isEmpty(patternMapper[dVal])) {
      const extractMapperValue = matchAndExtractMappedValue(patternMapper[dVal]);
      domainPatternClone = domainPatternClone.replace(`{{${dVal}}}`, extractMapperValue);
    }
  });
  return domainPatternClone;
};

export const loadItem = key => {
  try {
    const serializedState = localStorage.getItem(key);
    if (serializedState === null) {
      return undefined;
    }
    return JSON.parse(serializedState);
  } catch (err) {
    return undefined;
  }
};


export const saveItem = (key, value) => {
  try {
    const serializedState = JSON.stringify(value);
    localStorage.setItem(key, serializedState);
  } catch (err) {
    // Ignoring write error as of now
  }
};

export const convertToNestedObject = (flatObject) => {
  const nestedObject = {};
  Object.entries(flatObject).forEach(([key, value]) => {
      const parts = key.split('.');
      let current = nestedObject;
      parts.forEach((part, index) => {
          if (index === parts.length - 1) {
              current[part] = value;
          } else {
              current = current[part] = current[part] || {};
          }
      });
  });
  return nestedObject;
}

export const getBreadcrumbs = (currentState, formatMessage) => {
  const defaultBreadcrumbsTemplate = "{{selectedL1Module.productPackageLabel}}: {{selectedL1Module.displayLabel}} / {{selectedL2Module.displayLabel}}";
  const breadcrumbsTemplate = currentState.selectedL2Module?.breadcrumbsTemplate || currentState.selectedL1Module?.breadcrumbsTemplate || defaultBreadcrumbsTemplate;
  const regex = /{{\s*([^{}]+?)\s*}}/g;

  let resultsObject = [...breadcrumbsTemplate?.matchAll(regex)].reduce((acc, match) => {
      acc[match[1]] = formatMessage({ id: get(currentState, match[1], match[1]) });
      return acc;
  }, {});

  const breadcrumbsTemplateCompiler = Handlebars.compile(breadcrumbsTemplate, { noEscape: true });
  const result = breadcrumbsTemplateCompiler(convertToNestedObject(resultsObject));
  return result;
};

