import { createSelector } from 'reselect';
import { fromJS } from 'immutable';

import { selectAskAiraDomain } from '../selector';
import { AI_MEDIA_GENERATION } from './constants';

const selectMessagesDomain = (state = fromJS({})) =>
    selectAskAiraDomain(state)[AI_MEDIA_GENERATION];

const makeSelectImageDetail = () =>
    createSelector(selectMessagesDomain, (substate = fromJS({})) =>
        substate.toJS(),
    );
export { selectMessagesDomain, makeSelectImageDetail };
