import React from 'react';
import CapRow from '../CapRow';
import CapColumn from '../CapColumn';
import './_index.scss';
import { reducerActions, FormattedMessageWrapper } from './utils';
import CapIcon from '../CapIcon';
import CapHeading from '../CapHeading';

const QuickLinkSingleItem = ({
  icon,
  displayLabel,
  name,
  l1_itemHovered,
  disabled,
  url,
  redirectionType,
  targetLocation,
  itemSelected,
  itemHovered,
  isDisabled,
  showPremiumIcon,
  handleRedirectionAndModuleSelection,
  dispatchAction,
  redirectToDifferentDomain = false,
  domainPattern = '',
  patternMapper = {},
  ...props
}) => (
  <CapRow
    {...props}
    type='flex'
    align='middle'
    className={`l2-quick-link-container ${disabled ? 'nav-item-disabled' : ''} ${
      itemSelected ? 'itemSelected' : ''
    } ${!itemSelected && itemHovered && (!disabled && !isDisabled) ? 'itemHovered' : ''}`}
    onClick={(e) =>{
      e.stopPropagation()
      handleRedirectionAndModuleSelection({
        disabled : disabled || isDisabled,
        redirectionType,
        targetLocation,
        url,
        name,
        l1_itemHovered,
        redirectToDifferentDomain,
        domainPattern,
        patternMapper,
      })
    }
  }
    onMouseOver={() =>
      dispatchAction({
        type: reducerActions.hoveredL2Module,
        value: name,
      })
    }
    onMouseOut={() =>
      dispatchAction({
        type: reducerActions.hoveredL2Module,
        value: '',
      })
    }
  >
    <CapColumn span={2}>
      <CapIcon
        type={icon}
        className={`l2-quick-links-icon ${
          disabled || isDisabled ? 'nav-item-disabled' : ''
        } ${itemSelected ? 'itemSelected' : ''}`}
      />
    </CapColumn>
    <CapColumn span={22}>
      <CapHeading
        type="h6"
        className={`l2-quick-link-heading ${
          disabled || isDisabled ? 'nav-item-disabled' : ''
        } ${itemSelected ? 'itemSelected' : ''}`}
      >
        <FormattedMessageWrapper id={displayLabel} message={name} />
        {showPremiumIcon ? (
          <CapIcon type="premiumColored" className="premium-icon" />
        ) : (
          ''
        )}
      </CapHeading>
    </CapColumn>
  </CapRow>
);

export default QuickLinkSingleItem;
